import React from "react";
import Section from "../../common/Section";
import { Article, Header } from "./styled";



export default () => (
<div>
    <Header>O autorze</Header>
    <Section
    title="O mnie"
    body={(
       <Article>
        <p>
            <strong>Cze</strong>! W wolnych chwilach lubi dugie spacery po lesie, parku, 
            bardzo lubi te wyprawy do muzew, cho ostatnio nie mam za wiele wolnego czasu.
            Jeli chodzi o sport, moim ulubionym jest pywanie, kiedy te zrobiam kurs nurkowania.
            Prywatnie jestem wacicielk piknego i bardzo energicznego kotka.
        </p>
        <p>
            Zdecydowaam si na ten kurs, poniewa szukaam nowych umiejtnoci. Nauka programowania
            bya dla mnie trudna, ale w tej chwili mam <em>nieukrywan przyjemno</em> zaprezentowa wynik moich
            stara. Obecnie staram si powici wiecej czasu na rozwijanie moich moliwoci.
        </p>
       </Article>
)}
    />
</div>
);